<template>
  <div style="height: 100%">
    <el-header>
      <el-row style="text-align: right;">
        <el-col>
          <template v-if="!isEdit">
            <el-button type="primary" size="small" @click="copyHistory">文案历史</el-button>
            <el-button type="primary" size="small">职务排序</el-button>
            <el-button type="primary" size="small">新增</el-button>
            <el-button type="primary" size="small" @click="formEdit">编辑</el-button>
            <el-button type="primary" size="small">删除</el-button>
          </template>
          <template v-else>
            <el-button type="primary" size="small" @click="saveEditData">保存</el-button>
            <el-button type="primary" size="small" @click="cancelEdit">取消</el-button>
          </template>
        </el-col>
      </el-row>
    </el-header>
   <el-main class="grid-main" ref="gridmain">
     <el-row ref="formrow">
       <el-col>
         <el-main>
           <template v-if="!isEdit">
             <el-form class="commonsty-form" :label-width="formData.configs.labelWidth" :label-position="formData.configs.labelPosition" >
               <el-row>
                 <el-col v-for="(item,index) in formData.data" :span="item.span" :key="index">
                   <el-form-item :label="item.label" :prop="item.value">
                     <el-input
                       v-if="item.controlType == 3"
                       type="textarea"
                       :rows="2"
                       placeholder="请输入内容"
                       v-model="item.value"
                       :disabled="true">
                     </el-input>
                     <el-input v-model="item.value"  :disabled="true" v-if="item.controlType == 0"></el-input>
                   </el-form-item>
                 </el-col>
               </el-row>
             </el-form>
           </template>
           <template v-else>
             <el-form class="commonsty-form" autocomplete ='off' :label-width="formData.configs.labelWidth" :label-position="formData.configs.labelPosition">
               <el-row>
                 <el-col v-for="(item,index) in formData.data" :span="item.span" :key="index">
                   <el-form-item :label="item.label" :prop="item.value">
                     <el-date-picker
                       v-if="item.controlType == 1"
                       v-model="item.value"
                       type="date"
                       placeholder="选择日期">
                     </el-date-picker>
                     <el-tree-select
                       v-if="item.controlType == 2"
                       :props="props"
                       :options="optionData"
                       :clearable="isClearable"
                       :accordion="isAccordion"
                       v-model='item.treeId'/>
                     <el-input
                       v-if="item.controlType == 3"
                       type="textarea"
                       :rows="2"
                       placeholder="请输入内容"
                       v-model="item.value">
                     </el-input>
                     <el-input v-model="item.value"  v-if="item.controlType == 0"></el-input>
                   </el-form-item>
                 </el-col>
               </el-row>
             </el-form>
           </template>

         </el-main>
       </el-col>
     </el-row>
     <el-row>
       <el-col>
         <el-main class="main-tablebox" :style="{'height':height}">
             <el-table
               :data="tableData"
               size="small"
               style="width: 100%"
               height="100%">
               <el-table-column
                 v-for="item in tableDataProp"
                 :fixed="item.fixed"
                 :prop="item.prop"
                 :label="item.label"
                 :width="item.width"
                 :key="item.id">
               </el-table-column>
             </el-table>
         </el-main>
       </el-col>
     </el-row>
   </el-main>

    <copy-history ref="copyHistory"></copy-history>
  </div>
</template>

