                    {label:'日期',value:'2016-05-02',span:12,controlType:1},
                    {label:'机构级别',value:'方法',span:8,controlType:0},
                    {label:'备注',value:'方法',span:8,controlType:0},
                    {label:'备注',value:'个哈哈哈',span:8,controlType:0},
                    {label:'备注',value:'芬芳馥郁',span:24,controlType:3}
                  ]
                },
              formDataNew:{
                configs:{
                  labelWidth:'140px',
                  labelPosition:'left'
                },
                data:[
                  {label:'机构标识',value:'就当是',span:12,controlType:0},
                  {label:'机构全称',value:'发发发',treeId:7,span:12,controlType:2},
                  {label:'机构类别',value:'kks',span:12,controlType:0},
