                    {prop:'address',label:'地址',width:'200',id:'3',fixed:false},
                    {prop:'zip',label:'联系方式',width:'200',id:'4',fixed:false},
                    {prop:'city',label:'学校',width:'300',id:'5',fixed:false},
                    {prop:'province',label:'专业',width:'600',id:'6',fixed:false},
                ],
                tableData: [{
                    date: '2016-05-03',
                    name: '王小虎1',
                    province: '上海',
                    city: '普陀区',
                    address: '上海市普陀区金沙江路 1518 弄',
                    zip: 200333
                }, {
                    date: '2016-05-02',
                    name: '王小虎',
                    province: '上海',
                    city: '普陀区',
                    address: '上海市普陀区金沙江路 1518 弄',
