                }, {
                    date: '2016-05-04',
                    name: '王小虎',
                    province: '上海',
                    city: '普陀区',
                    address: '上海市普陀区金沙江路 1518 弄',
                    zip: 200333
                }, {
                    date: '2016-05-01',
                    name: '王小虎',
                    province: '上海',
                    city: '普陀区',
                    address: '上海市普陀区金沙江路 1518 弄',
                    zip: 200333
                }, {
                    date: '2016-05-08',
                    name: '王小虎',
                    province: '上海',
                    city: '普陀区',
                    address: '上海市普陀区金沙江路 1518 弄',
                    zip: 200333
                }, {
                    date: '2016-05-06',
                    name: '王小虎',
                    province: '上海',
                    city: '普陀区',
                    address: '上海市普陀区金沙江路 1518 弄',
                    zip: 200333
                }, {
                    date: '2016-05-07',
                    name: '王小虎',
                    province: '上海',
                    city: '普陀区',
                    address: '上海市普陀区金沙江路 1518 弄',
                    zip: 200333
                }],
                height:'',
                isEdit:false,
                props:{                // 配置项（必选）
                  value: 'id',
                  label: 'name',
                  children: 'children',
                  // disabled:true
                },
               isClearable:false,      // 可清空（可选）
               isAccordion:true,      // 可收起（可选）
               valueId:'',            // 初始ID（可选）
                list:[
                  {id:1,parentId:0,name:"一级菜单A",rank:1},
                  {id:2,parentId:0,name:"一级菜单B",rank:1},
                  {id:3,parentId:0,name:"一级菜单C",rank:1},
                  {id:4,parentId:1,name:"二级菜单A-A",rank:2},
                  {id:5,parentId:1,name:"二级菜单A-B",rank:2},
                  {id:6,parentId:2,name:"二级菜单B-A",rank:2},
                  {id:7,parentId:4,name:"三级菜单A-A-A",rank:3},
                  {id:8,parentId:7,name:"四级菜单A-A-A-A",rank:4},
                  {id:9,parentId:8,name:"五级菜单A-A-A-A-A",rank:5},
                  {id:10,parentId:9,name:"六级菜单A-A-A-A-A-A",rank:6},
                  {id:11,parentId:10,name:"七级菜单A-A-A-A-A-A-A",rank:7},
                  {id:12,parentId:11,name:"八级菜单A-A-A-A-A-A-A-A",rank:8},
                  {id:13,parentId:12,name:"九级菜单A-A-A-A-A-A-A-A-A",rank:9},
                  {id:14,parentId:13,name:"十级菜单A-A-A-A-A-A-A-A-A-A",rank:10},
                  {id:15,parentId:0,name:"一级菜单C",rank:1},
                  {id:16,parentId:0,name:"一级菜单C",rank:1},
                  {id:17,parentId:0,name:"一级菜单C",rank:1},
                  {id:18,parentId:0,name:"一级菜单C",rank:1},
                  {id:19,parentId:0,name:"一级菜单C",rank:1},
                  {id:20,parentId:0,name:"一级菜单C",rank:1},
                  {id:21,parentId:0,name:"一级菜单C",rank:1},
                  {id:22,parentId:0,name:"一级菜单C",rank:1},
                  {id:23,parentId:0,name:"一级菜单C",rank:1},
                  {id:24,parentId:0,name:"一级菜单C",rank:1},
                  {id:25,parentId:0,name:"一级菜单C",rank:1},
                  {id:26,parentId:0,name:"一级菜单C",rank:1},
                  {id:27,parentId:0,name:"一级菜单C",rank:1},
                  {id:28,parentId:0,name:"一级菜单C",rank:1},
                  {id:29,parentId:0,name:"一级菜单C",rank:1},
                  {id:30,parentId:0,name:"一级菜单C",rank:1},
                  {id:31,parentId:0,name:"一级菜单C",rank:1},
                  {id:32,parentId:0,name:"一级菜单C",rank:1},
                  {id:33,parentId:0,name:"一级菜单C",rank:1},
                  {id:34,parentId:0,name:"一级菜单C",rank:1},
                  {id:35,parentId:0,name:"一级菜单C",rank:1},
                  {id:36,parentId:0,name:"一级菜单C",rank:1},
                  {id:37,parentId:0,name:"一级菜单C",rank:1},
                  {id:38,parentId:0,name:"一级菜单C",rank:1},
                  {id:39,parentId:0,name:"一级菜单C",rank:1},
                  {id:40,parentId:0,name:"一级菜单end",rank:1}
                ]
            }
        },
        created(){
            this.$nextTick(function(){
                let allheight = this.$refs.gridmain.$el.offsetHeight
                let height= this.$refs.formrow.$el.offsetHeight;
                // 差9 是main-tablebox 的margin 和 边框
                let temp = allheight - height - 9
                this.height = temp + 'px'
            })
        },
        mounted(){

        },
        methods: {
           getData(){
             // var params = {
             //   personId:this.personId
             // }
             // this.$http({
             //   url: this.$http.adornUrl(this.API.getJobInformation),
             //   method: 'post',
             //   data: this.$http.adornData(params)
             // }).then(({data}) => {
             //   console.log(data)
             //   if(data && data.code===0){
             //     this.entryTree.entryTreeData = data.data
             //   }else{
             //     this.$message.error(data.msg);
             //   }
             // });
           },
            copyHistory(){
                this.$refs.copyHistory.initDialog()
            },
            formEdit(){
                //编辑
                this.isEdit = true
              this.$forceUpdate()
            },
            saveEditData(){
              //保存编辑数据
              console.log(this.formData)
            },
            cancelEdit(){
                //取消编辑
                this.isEdit = false
              this.formData = Object.assign({},this.formDataNew)
            }
        },
        computed:{
          /* 转树形数据 */
          optionData(){
            let cloneData = JSON.parse(JSON.stringify(this.list))      // 对源数据深度克隆
            return  cloneData.filter(father=>{                      // 循环所有项，并添加children属性
              let branchArr = cloneData.filter(child=>father.id == child.parentId);       // 返回每一项的子级数组
              branchArr.length>0 ? father.children=branchArr : ''   //给父级添加一个children属性，并赋值
              return father.parentId==0;      //返回第一层
            });
          }
        },

    }
</script>

<style lang="scss" scoped type="text/stylus">
  .el-header{
    height: 44px!important;
    line-height: 44px;
  }
  .el-main{
    padding: 0;
    .main-table{
      width: 100%;
      tbody {
        tr{
          height: 30px;
          td{
            border: 1px solid red;
            .el-form-item{
              margin-bottom: 0;
            }
          }
        }
      }
    }
  }


  .el-input--medium.is-disabled{
      /*background:#FFFFF3;*/
  }
  .grid-main{
    padding: 0 16px;
    position: absolute;
    top: 44px;
    left: 0;
    right: 0;
    bottom: 0;
  }
  /*表格样式*/
  .main-tablebox{
    margin:0 4px 8px 4px;
    border: 1px solid #d1cece;
  }
  //input表格样式
  >>> .el-textarea.is-disabled .el-textarea__inner,>>> .el-textarea__inner{
    background: #fff;
    border:none;
  }
  .commonsty-form .el-form-item{
    position:relative;
    min-height:38px;
  }
  .commonsty-form .el-form-item /deep/ .el-form-item__label{
    display:inline-block;
    height : 100%;
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
  }

</style>



